<template>
<div class="all">
  <div class="top"></div>

  <div class="content" v-for="list in lists" >
    <div class="he">

      <img :src="list.header" class="left">

      <div class="mid">
        <p class="username">{{list.username}}</p>
        <p>
          <img src="../assets/时间.png" style="height: 14px;width: 12px">
          <span>{{list.createTime}}</span>

          <img src="../assets/声音.png" style="height: 14px;width: 12px">
          <span>公开</span>
        </p>
      </div>
      <img src="../assets/党员互动icon.png" class="right">
    </div>

    <div class="ping">{{list.content}}</div>

    <div class="huifu">
      <router-link :to="'/dangyuan1_huifu?id='+list._id" >回复</router-link>
    </div>

  </div>
</div>
</template>

<script>
    export default {
        name: "dangyuan",
    data(){
      return{
        lists:[
          {
            content: "",
            createTime: "",
            header: "",
            updateTime: "",
            user_id: "",
            username: "",
            _id: "",
        }
        ]
      }
    },
    created(){
      this.getlist();
    },
    methods:{
      getlist(){
        this.$axios.get("getlist").then(res=>{
          // console.log(res.data);
          this.lists= res.data
        })
      },
    }
  }
</script>

<style scoped lang="less">
.all{
  width: 100%;
  .top{
    height: 43px;
    width: 100%;
  }
  .content{
    height: 111px;
    width: 98%;
    margin: 0 auto 0 auto;
    padding: 10px 5px;

    .he{
      position: relative;
      width: 95%;
      margin-top: 10px;
      padding-top: 5px;
      .ping{
        width: 98%;
      }
    .mid{
      margin-left: 10px;
      .username{
        font-size: 16px;
        margin-bottom: 2px;
      }
      p{
        span{
          margin-left: 5px;
        }
      }
    }
    .left{
      width: 37px;
      height: 37px;
      border-radius: 50%;
    }
    .right{
      position: absolute;
      right: 3px;
      top: 5px;
    }
    img{
      width: 66px;
      height: 26px;
    }

      display: flex;
    }
    .ping{
      width: 100%;
      margin: 10px 0;
      text-align: left;
      font-size: 18px;
    }
    .huifu{
      width: 95%;
      text-align: right;
      margin: 10px 10px 0 0;
      font-size: 14px;
    }
  }
}

</style>



